.page-container {
  max-width: 1280px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 45px 200px 60px;
  font-family: 'Inter', 'Helvetica Neue', Arial, sans-serif;
}

@media screen and (max-width: 1279px) {
  .page-container {
    max-width: 320px;
    padding: 28px 0 36px;
  }
}